class Api {
  constructor(url, headers) {
    this._url = url;
    this._headers = headers;
  }

  // Ð¿Ñ€Ð¾Ð²ÐµÑ€Ð¸Ñ‚ÑŒ ÑÑ‚Ð°Ñ‚ÑƒÑ Ð·Ð°Ð¿Ñ€Ð¾ÑÐ°
  _checkStatus(res) {
    if (res.ok) {
      return res.json();
    } else {
      return Promise.reject(`${res.status} ${res.statusText}`);
    };
  };

  // Ð¿Ñ€Ð¸Ð¼ÐµÑ€ Ð½Ð°Ð¿Ð¸ÑÐ°Ð½Ð¸Ñ Ð·Ð°Ð¿Ñ€Ð¾ÑÐ° Ðº ÑÐµÑ€Ð²ÐµÑ€Ñƒ
  sendMessage(userEmail, message) {
    return fetch(this._url + '/messages', {
      method: 'POST',
      headers: this._headers,
      body: JSON.stringify({
        userEmail: userEmail,
        message: message
      })
    }).then(this._checkStatus);
  };
};

// Ð·Ð´ÐµÑÑŒ Ð¼Ñ‹ ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ api Ñ Ð°Ð´Ñ€ÐµÑÐ¾Ð¼ ÑÐµÑ€Ð²ÐµÑ€Ð°
const api = new Api('https://your-backend.ru');

export default api;